.top {
  height: 50px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.top .logo {
  color: #6439ff;
  font-size: 20px;
  font-weight: bold;
  color: white;
}

.top hr {
  height: 0;
  border: 0.5px solid;
}

.center {
  padding-left: 20px;
}

.center span {
  margin: 0;
  padding: 0;
  gap: 20px;
  display: flex;
  padding-top: 15px;
  color: beige;
}
.fram{
  color: white;
  font-size: 20px;
}
.hot{
  -webkit-box-shadow: 2px 4px 10px 1px rgba(27, 11, 241, 0.47);
  box-shadow: 2px 4px 10px 1px rgba(201, 201, 201, 0.47);
  background: linear-gradient(106.83deg, #1e00ff 5.59%, #5828E0 90.5%),
  linear-gradient(0deg, #26214D, #26214D);
  border-radius: 10px;
  width: 1100px;
  height: 130px;
  margin-top: 30px;
}
